export class TournamentPage extends React.Component<IProps, IState> {
    private finalBracketChars: string[];

    public constructor(props: IProps) {
        super(props);
        const n = CHARS.length;
        const factors = GET_FACTORS(n);
        const startingIdx = Math.floor(factors.length*0.65);
        this.state = {
            tournamentState: TournamentState.SETUP,
            characters: CHARS,
            prelimConfig: [factors[startingIdx], n/factors[startingIdx]],
            prelimToUB: 2,
            prelimToLB: 2,
        };
        this.finalBracketChars = [];
    }

    public override componentDidMount(): void {
        console.log(`TournamentPage MOUNT`);
        document.title = `${DEV_STRING_PRE}Best Girl Tierlist Tournament`;
    }

    private changePreliminaryGroupConfig = (event: React.FormEvent<HTMLSelectElement>) => {
        if(this.state.tournamentState === TournamentState.SETUP) {
            event.preventDefault();
            const nGroups = +event.currentTarget.value;
            const nChars = this.state.characters.length/nGroups;
            this.setState({ tournamentState: TournamentState.SETUP, prelimConfig: [nGroups, nChars], prelimToUB: 1, prelimToLB: 1 });
        }
    }

    private changePreliminaryUBAdvance = (event: React.FormEvent<HTMLSelectElement>) => {
        if(this.state.tournamentState === TournamentState.SETUP) {
            event.preventDefault();
            const n = +event.currentTarget.value;
            this.setState({ tournamentState: TournamentState.SETUP, prelimToUB: n });
        }
    }

    private changePreliminaryLBAdvance = (event: React.FormEvent<HTMLSelectElement>) => {
        if(this.state.tournamentState === TournamentState.SETUP) {
            event.preventDefault();
            const n = +event.currentTarget.value;
            this.setState({ tournamentState: TournamentState.SETUP, prelimToLB: n });
        }
    }

    private renderSetup(): React.ReactNode | undefined {
        if(this.state.tournamentState !== TournamentState.SETUP) {
            return;
        }
        const setupState: IStateSetup = this.state;
        const n = setupState.characters.length;
        const factors = GET_FACTORS(n);
        const startingIdx = Math.floor(factors.length*0.65);
        const ubn = setupState.prelimConfig[0]*this.state.prelimToUB;
        const lbn = setupState.prelimConfig[0]*this.state.prelimToLB;
        return (
            <div className={styles.container}>
                <div>{`${n} characters total`}</div>
                <div>
                    {setupState.characters.map(char => <Character character={char} />)}
                </div>
                <div>
                    <div>{`PRELIMINARIES (${n} characters)`}</div>
                    <div>
                        <select onChange={this.changePreliminaryGroupConfig}>
                            {factors.map((factor, idx) =>
                                <option key={idx} value={factor} selected={idx === startingIdx}>{`${factor} groups of ${n/factor} characters each`}</option>
                            )}
                        </select>
                    </div>
                    <div>
                        <select onChange={this.changePreliminaryUBAdvance}>
                            {[...Array(Math.ceil((setupState.prelimConfig[1]-1)/2)+1).keys()].slice(1).map((i) =>
                                <option key={i} value={i} selected={i === setupState.prelimToUB}>{`Top ${i} to UB`}</option>
                            )}
                        </select>
                    </div>
                    <div>
                        <select onChange={this.changePreliminaryLBAdvance}>
                            {[...Array(Math.ceil((setupState.prelimConfig[1]-1)/2)+1).keys()].slice(1).map((i) =>
                                <option key={i} value={i} selected={i === setupState.prelimToLB}>{`Next ${i} to LB`}</option>
                            )}
                        </select>
                    </div>
                    <div>
                        {`Eliminate remaining ${setupState.prelimConfig[1] - setupState.prelimToUB - setupState.prelimToLB}`}
                    </div>
                </div>
                <br/>
                <div>
                    <div>{`UPPER BRACKET GROUPS (${ubn} characters)`}</div>
                    <div>
                        6 groups of 8 characters each
                    </div>
                    <div>
                        Top 2 to final bracket
                    </div>
                    <div>
                        Everyone else to LCQ1
                    </div>
                </div>
                <br/>
                <div>
                    <div>{`LOWER BRACKET GROUPS (${lbn} characters)`}</div>
                    <div>
                        6 groups of 8 characters each
                    </div>
                    <div>
                        Top 2 to LCQ1
                    </div>
                    <div>
                        Everyone else eliminated
                    </div>
                </div>
                <br/>
                <div>
                    <div>{`LCQ1 (${48} characters)`}</div>
                    <div>
                        6 groups of 8 characters each
                    </div>
                    <div>
                        Top 2 to LCQ2
                    </div>
                    <div>
                        Everyone else eliminated
                    </div>
                </div>
                <br/>
                <div>
                    <div>{`LCQ2 (${12} characters)`}</div>
                    <div>
                        Top 4 to final bracket
                    </div>
                    <div>
                        Everyone else eliminated
                    </div>
                </div>
                <br/>
                <div onClick={this.toPrelims}>To Preliminaries</div>
            </div>
        );
    }

    private toPrelims = () => {
        if(this.state.tournamentState !== TournamentState.SETUP) {
            return;
        }
        const groups: {[char: string]: number[]}[] = [];
        const charactersRemaining: number[] = [...Array(this.state.characters.length).keys()];
        for(let groupI = 0; groupI < this.state.prelimConfig[0]; groupI++) {
            const group: {[char: string]: number[]} = {};
            for(let charI = 0; charI < this.state.prelimConfig[1]; charI++) {
                group[ this.state.characters[ charactersRemaining.splice(RAND_INT(0, charactersRemaining.length), 1)[0] ] ] = [];
            }
            groups.push(group);
        }
        this.setState({ tournamentState: TournamentState.PRELIMS, groups: groups });
    }
    
    private renderPrelims(): React.ReactNode | undefined {
        if(this.state.tournamentState !== TournamentState.PRELIMS) {
            return;
        }
        const prelimsState: IStatePrelims = this.state;
        return (
            <div className={styles.container}>
                <div>Prelims</div>
                {prelimsState.groups.map((group) =>
                    <div className={styles.group}>
                        {Object.keys(group).map(char => <Character character={char} />)}
                    </div>
                )}
                <div onClick={this.toGroups}>To Groups</div>
            </div>
        );
    }

    private toGroups = () => {
        if(this.state.tournamentState !== TournamentState.PRELIMS) {
            return;
        }
        const ubGroups: {[char: string]: number[]}[] = [];
        const lbGroups: {[char: string]: number[]}[] = [];
        for(let i = 0; i < 6; i++) {
            ubGroups.push({});
            lbGroups.push({});
        }
        let groupI = 0;
        for(let group of this.state.groups) {
            let charI = 0;
            for(let char in group) {
                if(charI < 2) {
                    ubGroups[(groupI+charI) % 6][char] = [];
                } else if(charI < 4) {
                    lbGroups[(groupI+charI) % 6][char] = [];
                } else {
                    break;
                }
                charI++;
            }
            groupI++;
        }
        this.setState({ tournamentState: TournamentState.GROUPS, ubGroups: ubGroups, lbGroups: lbGroups });
    }
    
    private renderGroups(): React.ReactNode | undefined {
        if(this.state.tournamentState !== TournamentState.GROUPS) {
            return;
        }
        const groupsState: IStateGroups = this.state;
        return (
            <div className={styles.container}>
                <div>UB Groups</div>
                {groupsState.ubGroups.map((group) =>
                    <div className={styles.group}>
                        {Object.keys(group).map(char => <Character character={char} />)}
                    </div>
                )}
                <div>LB Groups</div>
                {groupsState.lbGroups.map((group) =>
                    <div className={styles.group}>
                        {Object.keys(group).map(char => <Character character={char} />)}
                    </div>
                )}
                <div onClick={this.toLCQ1}>To LCQ1</div>
            </div>
        );
    }

    private toLCQ1 = () => {
        if(this.state.tournamentState !== TournamentState.GROUPS) {
            return;
        }
        const charsToFinalBracket: string[] = [];
        const groups: {[char: string]: number[]}[] = [];
        for(let i = 0; i < 6; i++) {
            groups.push({});
        }
        let groupI = 0;
        for(let group of this.state.ubGroups) {
            let charI = 0;
            for(let char in group) {
                if(charI < 2) {
                    charsToFinalBracket.push(char);
                } else {
                    groups[(groupI+charI) % 6][char] = [];
                }
                charI++;
            }
            groupI++;
        }
        for(let group of this.state.lbGroups) {
            let charI = 0;
            for(let char in group) {
                if(charI < 2) {
                    groups[(groupI+charI) % 6][char] = [];
                }
                charI++;
            }
            groupI++;
        }
        this.finalBracketChars = charsToFinalBracket;
        this.setState({ tournamentState: TournamentState.LCQ1, groups: groups });
    }
    
    private renderLCQ1(): React.ReactNode | undefined {
        if(this.state.tournamentState !== TournamentState.LCQ1) {
            return;
        }
        const lcq1State: IStateLCQ1 = this.state;
        return (
            <div className={styles.container}>
                <div>LCQ1</div>
                {lcq1State.groups.map((group) =>
                    <div className={styles.group}>
                        {Object.keys(group).map(char => <Character character={char} />)}
                    </div>
                )}
                <div onClick={this.toLCQ2}>To LCQ2</div>
            </div>
        );
    }

    private toLCQ2 = () => {
        if(this.state.tournamentState !== TournamentState.LCQ1) {
            return;
        }
        const finalGroup: {[char: string]: number[]} = {};
        for(let group of this.state.groups) {
            let charI = 0;
            for(let char in group) {
                if(charI < 2) {
                    finalGroup[char] = [];
                }
                charI++;
            }
        }
        this.setState({ tournamentState: TournamentState.LCQ2, group: finalGroup });
    }
    
    private renderLCQ2(): React.ReactNode | undefined {
        if(this.state.tournamentState !== TournamentState.LCQ2) {
            return;
        }
        const lcq2State: IStateLCQ2 = this.state;
        return (
            <div className={styles.container}>
                <div>LCQ2</div>
                <div className={styles.group}>
                    {Object.keys(lcq2State.group).map(char => <Character character={char} />)}
                </div>
                <div onClick={this.toFinalBracket}>To Final Bracket</div>
            </div>
        );
    }

    private toFinalBracket = () => {
        if(this.state.tournamentState !== TournamentState.LCQ2) {
            return;
        }
        const finalGroup: {[char: string]: number[]} = {};
        for(let char of this.finalBracketChars) {
            finalGroup[char] = [];
        }
        let charI = 0;
        for(let char in this.state.group) {
            if(charI < 4) {
                finalGroup[char] = [];
            }
            charI++;
        }
        this.setState({ tournamentState: TournamentState.FINAL_BRACKET, group: finalGroup });
    }
    
    private renderFinalBracket(): React.ReactNode | undefined {
        if(this.state.tournamentState !== TournamentState.FINAL_BRACKET) {
            return;
        }
        const finalBracketState: IStateFinalBracket = this.state;
        return (
            <div className={styles.container}>
                <div>Final Bracket</div>
                <div className={styles.group}>
                    {Object.keys(finalBracketState.group).map(char => <Character character={char} />)}
                </div>
            </div>
        );
    }

    public render(): React.ReactNode {
        switch(this.state.tournamentState) {
            case TournamentState.SETUP:
                return this.renderSetup();
            case TournamentState.PRELIMS:
                return this.renderPrelims();
            case TournamentState.GROUPS:
                return this.renderGroups();
            case TournamentState.LCQ1:
                return this.renderLCQ1();
            case TournamentState.LCQ2:
                return this.renderLCQ2();
            case TournamentState.FINAL_BRACKET:
                return this.renderFinalBracket();
        }
    }
}
